
/* Sets style for footer and makes it stick to bottom */
footer {
    background-color: #17252A;
    text-align: center;
}

footer span {
    margin-top: 10px;
    padding: 5px;
    font-size: 35px;
    color: #FEFFFF;
}

/* Sets style for Footer font */
footer h6 {
    text-align: center;
    padding-top: 5px;
    padding-bottom: 5px;
    color: #FEFFFF;
}

